___CSS_LOADER_EXPORT___.push([module.id, ".login-form[data-v-103a0b69]{max-width:600px}.login_bg[data-v-103a0b69]{background:radial-gradient(circle at 50% 50%,#ed1c24,#ffd503)}.btn_color[data-v-103a0b69]{background:#e63546!important;color:#fff}.label_color[data-v-103a0b69]{color:#e63546}", "",{"version":3,"sources":["webpack://./resources/js/auth/pages/otp_input.vue"],"names":[],"mappings":"AA0FA,6BACA,eACA,CAEA,2BAEA,6DACA,CAEA,4BAEA,4BAAA,CADA,UAEA,CAEA,8BACA,aACA","sourcesContent":["<template>\r\n  <div>\r\n    <v-app class=\"login_bg\">\r\n      <v-main>\r\n        <v-container fill-height>\r\n          <v-layout align-center justify-center>\r\n            <v-flex class=\"login-form text-xs-center\">\r\n              <v-card>\r\n                <v-card-text>\r\n                  <form @submit.prevent=\"verifyOtp()\">\r\n                    <div class=\"d-flex align-items-center justify-content-center mb-3\">\r\n                      <img src=\"/all-assets/common/logo/cpb/cpfivestar.png\" alt height=\"50px\" />\r\n                      <h3 class=\"text-center label_color ml-4\">Reset Password</h3>\r\n                    </div>\r\n                    <v-alert dense text type=\"success\">\r\n                      An OTP has been sent to this\r\n                      <strong>{{ $route.params.number }}</strong> number\r\n                    </v-alert>\r\n\r\n                    <h5>Enter OTP</h5>\r\n                    <v-otp-input></v-otp-input>\r\n\r\n                    <v-btn block small color=\"primary\" type=\"submit\">Submit</v-btn>\r\n                  </form>\r\n                </v-card-text>\r\n              </v-card>\r\n            </v-flex>\r\n          </v-layout>\r\n        </v-container>\r\n      </v-main>\r\n    </v-app>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport Form from \"vform\";\r\nexport default {\r\n  data() {\r\n    return {\r\n      form: new Form({\r\n        otp: \"\",\r\n        number: \"\"\r\n      })\r\n    };\r\n  },\r\n\r\n  methods: {\r\n    verifyOtp() {\r\n      this.form.post(\"/verify_otp\").then(response => {\r\n        Swal.fire(\r\n          response.data.data,\r\n          response.data.msg,\r\n          response.data.status\r\n        ).then(() => {\r\n          if (response.data.status == \"success\") {\r\n            console.log(this.$route.params.reset_type);\r\n            // this.$router.go(\"/login/new-password\");\r\n            this.$router.push({\r\n              name: \"new_password\",\r\n              params: {\r\n                owner_id: response.data.owner_id,\r\n                reset_type: this.$route.params.reset_type,\r\n                cv_code: this.$route.params.cv_code\r\n              }\r\n            });\r\n          }\r\n          if (response.data.expire == \"expired\") {\r\n            this.$router.go({\r\n              name: \"Login\"\r\n            });\r\n          }\r\n        });\r\n      });\r\n    }\r\n  },\r\n\r\n  created() {\r\n    if (!this.$route.params.number) {\r\n      this.$router.push({\r\n        name: \"resetPassword\"\r\n      });\r\n    } else {\r\n      this.form.number = this.$route.params.number;\r\n    }\r\n  }\r\n};\r\n</script>\r\n\r\n\r\n<style scoped>\r\n.login-form {\r\n  max-width: 600px;\r\n}\r\n\r\n.login_bg {\r\n  /* background: #990f02; */\r\n  background: radial-gradient(circle at 50% 50%, #ed1c24, #ffd503);\r\n}\r\n\r\n.btn_color {\r\n  color: white;\r\n  background: #e63546 !important;\r\n}\r\n\r\n.label_color {\r\n  color: #e63546;\r\n}\r\n</style>"],"sourceRoot":""}]);
